<template>
  <div class="filestorage">
        <vue-headful
            title="0xsp Mongoose | File Storage"
            description=""
        />    
    <div class="row">
      <div class="col-md-12">
        <div class="card">
          <h4 class="card-header">File Storage</h4>
          <div class="card-body">
        <b-table
          id="my-table"
          :items="files"
          :per-page="perPage"
          :current-page="currentPage"
          striped hover
          :fields="fields"
        >
        <template v-slot:cell(actions)="row">
                      <p-button
                        class="mr-2"
                        size="sm"
                        type="info"
                        @click.native.prevent="downloadFile(row.item.id)"
                      >
                        <i class="ti-download"></i>
                      </p-button>
                      <p-button
                        size="sm"
                        type="danger"
                        @click.native.prevent="deleteFile(row.item.id)"
                        >
                        <i class="ti-trash"></i>
                      </p-button>
                        
            </template>

        </b-table>


          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            align="center"
            pills size="lg"
          ></b-pagination>

          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      files: [],
      currentPage:1,
      perPage:7,
      inputs:{},
        fields: [
          { key: 'file_name', label: 'File Name', sortable: true, sortDirection: 'desc' },
          { key: 'file_type', label: 'Type', sortable: true, class: 'text-center' },
          { key: 'actions', label: 'Actions' }
        ],      
    };
  },
  methods: {
    async fetchFiles() {
      this.axios.get("/mydashboard/getDownloadCenter").then((response) => {
        this.files = response.data.data;
      });
    },
    downloadFile(id) {
      var headers = { responseType: "blob" };
      this.axios.get("/mydashboard/downloadFile/" + id).then((response) => {
        var fileName = response.data.fullFileName;
        this.axios
          .get(
            this.axios.defaults.baseURL + "/uploads/" + response.data.fullFileName,
            headers
          )
          .then((res) => {
            var fileURL = window.URL.createObjectURL(new Blob([res.data]));
            var fileLink = document.createElement("a");

            fileLink.href = fileURL;
            fileLink.setAttribute("download", fileName);
            document.body.appendChild(fileLink);

            fileLink.click();
          });
      });
    },

    deleteFile(id){
      this.axios.post("/mydashboard/downloadFile/" + id).then(response => {
          if(response.data === "OK"){
              this.$toasted.show("File Deleted Successfully!", { 
                theme: "toasted-primary", 
                position: "top-center", 
                duration : 1500,
                type:'success'
          });       
            this.fetchFiles();
          }
      });
    }
  },
  created() {
    this.fetchFiles();
  },
  computed: {
    rows() {
      return this.files.length
    }
  }
};
</script>
